.footer-title {
    font-size: 35px;
    font-family: "Mitr-SemiBold";
    font-weight: 600;
    line-height: 48px;
    margin-bottom: 24px;
}

.footer-avaliable {
    .footer-avaliable-text {
        font-size: 16px;
    }
    .footer-avaliable-website {
        width: 251px;
        height: 48px;
        font-size: 18px;
        font-family: "Mitr-Medium";
        font-weight: 500;
        line-height: 48px;
        border-radius: 24px 24px 24px 24px;
        border: 1px solid #FAFAFF;
        margin: 0 auto;
        margin-top: 16px;
        margin-bottom: 24px;
        img {
            position: relative;
            top: 6px;
            margin-right: 16px;
        }
    }
}

.footer-line {
    img {
        height: 48px;
    }
}

.footer-text {
    font-size: 16px;
    font-weight: 300;
    color: #F2F2FF;
    line-height: 32px;
    &.footer-text-one {
        margin: 24px 0;
    }
    &.footer-text-two {
        margin: 24px auto;
    }
}

.media-link {
    a {
        margin: 0 24px;
    }
}
