#part3d {
    position: relative;
    background: #FAFAFF;
    border-radius: 0px 0px 120px 120px;
    padding-top: 66px;
    padding-bottom: 80px;
    
    .part3d-content {
        width: 100%;
        // height: 1462px;

        .part3d-main {
            box-sizing: border-box;
            padding-top: 80px;
            text-align: center;
            .main-top {
                width: 525px;
                margin: 0 auto;
                text-align: center;
                position: relative;
                img.icon-360 {
                    height: 36px;
                    position: absolute;
                    top: -38px;
                    left: 50%;
                    margin-left: -60px;
                }
                img.model {
                    height: 400px;
                    margin-top: 14px;
                }
                .eq-item {
                    position: absolute;
                    &.eq-item01 {
                        left: -85px;
                        top: -100px;
                    }
                    &.eq-item02 {
                        right: -202px;
                        top: 100px;
                    }
                    &.eq-item03 {
                        left: -203px;
                        bottom: 92px;
                    }
                }
            }

            .dino {
                margin-top: 28px;
            }
            
            .main-bottom {
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                align-items: center;
                position: relative;
                top: -26px;
                padding: 0 48px;

                .btn-trading {
                    width: 360px;
                    height: 64px;
                    line-height: 64px;
                    background: linear-gradient(135deg, #F2801D 0%, #F24404 100%);
                    box-shadow: 0px 8px 16px 1px rgba(242, 68, 4, 0.20000000298023224);
                    border-radius: 32px;
                    text-align: center;
                    font-size: 24px;
                    font-family: "Mitr-Medium";
                    font-weight: 500;
                    color: #FFF8F2;
                    img {
                        position: relative;
                        top: 3px;
                        margin-left: 8px;
                    }
                    position: relative;
                    bottom: 29px;
                }

                .main-bottom-left, .main-bottom-right {
                    display: flex;
                    align-items: center;
                }
                img {
                    &.model {
                        height: 160px;
                    }
                    &.arrow {
                        margin-top: 40px;
                    }
                    &.arrow-left {
                        margin-right: 20px;
                    }
                    &.arrow-right {
                        margin-left: 20px;
                    }
                    
                }
            }
        }
        

        .eq-box {
            width: 100%;
            height: 120px;
            position: relative;
            margin-top: 100px;
            img {
                width: 120px;
                height: 120px;
                margin-right: 24px;
            }

            .eq-more {
                position: absolute;
                top: 0;
                right: 0;
                width: 120px;
                height: 120px;
                background: linear-gradient(135deg, #F2801D 0%, #F24404 100%);
                border-radius: 0 16px 16px 0;
                text-align: center;

                // font
                line-height: 120px;
                font-size: 24px;
                font-family: "Mitr-Medium";
                font-weight: 500;
                color: #FFF8F2;
                cursor: pointer;
            }
        }

        
    }
}